import Vue from 'vue'

import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
  // ,vuedata
  state: {
    cart:{
      cart_length: 0,
    },
  },
  // ,vuemethods
  mutations: {
     // 
    add_cart(state,data){
      state.cart.cart_length = data;

    }
  }
});



